.button {
  background: #292929;
  border-radius: 40px;
  display: block;
  text-decoration: none;
  color: #fff;
  text-align: center;
  height: 56px;
  line-height: 56px;
  font-weight: 700;
  margin-bottom: 120px;

  &--form {
    margin-bottom: 50px;

    &:hover {
    background: #fff;
    color: #292929;
    border-color: #292929;
    }
  }
}
